
body {
    background: #485563;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #29323c, #485563);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #29323c, #485563); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    padding-bottom: 100px;
    
    font-family: 'Oswald', sans-serif;

}

.App-header {
    position: relative;
    height: 130px;
    background: #fff;
    color: #29323c;
    text-align: center;
    font-size: 57px;
    /* line-height: 120px; */
    position: fixed;
    display: block;
    width: 100%;
    z-index: 4;
    border-bottom: 2px solid #ff6d88;
    font-weight: bold;
    /* text-transform: uppercase; */
}
.logo {
    display: block;
    width: 100%;
}
.App-header input {
    position: absolute;
    height: 38px;
    border-radius: 19px;
    width: 300px;
    text-align: center;
    background-image: linear-gradient(to bottom, rgba(245, 247, 250, .5) 0%, rgba(195, 207, 226, .5) 100%);
    border: none;
    /* border-bottom: 1px solid #ddd; */
    left: 50%;
    transform: translateX(-50%);
    bottom: 10px;
}
.App-header input:focus {
    outline: none;
}
@media (max-width: 768px) {
    .App-header {
        font-size: 30px;
        height: 90px;
        line-height: 90px;
    }
}
.App-header span {
    color: #ff6d88;
}
.loader{
    background: #29323c;
    position: fixed;
    top: 0;
    bottom: 0;
    width: 100%;
    height: 100vh;
}
.loader img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%)
}
