.gotoTop {
    position: fixed;
    background: #ff6f89;
    bottom: 120px;
    left: 50%;
    z-index: 15;
    width: 40px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    border-radius: 50%;
    margin-left: -20px;
    color: #fff;
    svg {
        fill: aqua;
    }
}